import axios from 'axios'
import PubSub from 'pubsub-js'

const http = axios.create({
  baseURL: '/api/',
  timeout: 10000,
})

/*  cookie  token */
const result = document.cookie.match(/token=(QJWT [\w\.\-]+)/) // eslint-disable-line

/*  cookie  token */
document.cookie = `token=none; expires=${new Date(0)};`

/*  cookie  token  cookie  */
/* token  token  localStorage  cookie  */
/* token localStorage  token */
if (result) {
  window.localStorage.setItem('token', result[1])
}

/*  storage  */
window.addEventListener('storage', (event) => {
  if (event.key === 'token') {
    /*  token  */
    PubSub.publish('FETCH/LOCAL_STORAGE_TOKEN_CHANGE')
  }
})

/*  localStorage  token */
PubSub.subscribe('CONTAINERS/HEADER/LOGOUT_SUCCEED', () => {
  window.localStorage.removeItem('token')
})

const getDynamicConfig = function () {
  /* token  localStorage  */
  const token = window.localStorage.getItem('token')

  return {
    headers: {
      authorization: token,
    },
  }
}

function axiosGet(path, config) {
  return http
    .get(path, { ...getDynamicConfig(), ...config })
    .then((response) => response.data)
    .catch((error) => {
      throw error
    })
}

function axiosPost(path, params, config) {
  return http
    .post(path, params, { ...getDynamicConfig(), ...config })
    .then((response) => response.data)
    .catch((error) => {
      throw error
    })
}

function axiosPut(path, params, config) {
  return http
    .put(path, params, { ...getDynamicConfig(), ...config })
    .then((response) => response.data)
    .catch((error) => {
      throw error
    })
}

function axiosDelete(path, config) {
  return http
    .delete(path, { ...getDynamicConfig(), ...config })
    .then((response) => response.data)
    .catch((error) => {
      throw error
    })
}

/*  */
/* eslint-disable */
export {
  getDynamicConfig,
}
/* eslint-disable */

/*  http  */
export default {
  instance: http,
  get: axiosGet,
  post: axiosPost,
  put: axiosPut,
  delete: axiosDelete,
}



